// 工具库
class Utils {

    // 获取cookies里面的token
    getCookies(t) {
        let token = "";
        for (let i = 0; i < 3; i++) {
            let price = t.$cookies.get("m" + i);

            if (price == null) {

                return false;
            }
            token += price + ".";
        }

        return token.slice(0, -1);
    }

    // 第一个参数是this，第二个参数是提示信息
    showLoading(t, message = "加载中") {
        // 开启loading动画
        t.$toast.loading({
            duration: 0,
            message,
            forbidClick: true
        });
    }

    clearLoading(t) {
        t.$toast.clear();
    }

    removeCookies(t) {
        let time = new Date('1997-01-01');

        for (let i = 0; i < 3; i++) {
            t.$cookies.set("m" + i, '', time);
        }
    }

    // 本地存储
    // 设置
    localSet(key, val) {
        window.localStorage.setItem(key, JSON.stringify(val));
    }

    // 获取
    localGet(key) {
        return JSON.parse(window.localStorage.getItem(key));
    }


}

export default new Utils();