<template>
  <div class="home">
    <div class="top">
      <div class="top-bg">
        <div class="top-content">
          <p class="title">{{new Date()|makeNoon}}好,{{username ? username : 'xxx'}}</p>
          <p class="content">现在是喝咖啡的好时间！</p>
        </div>
      </div>
      <div class="top-banner">
        <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
          <van-swipe-item v-for="(item,index) in banners" :key="index">
            <div
              class="banner"
              v-lazy:background-image="item.bannerImg"
              :style="{backgroundImage:'url(' + item.bannerImg + ')'}"
            ></div>
          </van-swipe-item>
        </van-swipe>
      </div>
    </div>
    <div class="middle">
      <div class="middle-list">
        <div class="middle-item" v-for="(item,index) in classify" :key="index">
          <div @click="toMenu(item)" class="item-img">
            <img v-lazy="item.pic" :src="item.pic" alt />
          </div>
          <div class="item-title">{{item.typeDesc}}</div>
        </div>
      </div>
    </div>
    <div class="recomend-title">
      <div>为您推荐</div>
    </div>
    <div class="recomend-box">
      <div class="recomend-list">
        <div
          class="recomend-item"
          @click="toDetail(item)"
          v-for="(item,index) in hots"
          :key="index"
        >
          <div class="recomend-img">
            <img v-lazy="item.smallImg" :src="item.smallImg" alt />
          </div>
          <div class="recomend-t">{{item.name}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Api from "../api/api";
import Util from "../util/utils";
export default {
  data() {
    return {
      username: ""
    };
  },
  created() {
    //开始加载中
    this.$toast.loading({
      duration: 0,
      message: "加载中...",
      forbidClick: true
    });

    let self = this; //保存this
    // 多个axios一起执行
    this.axios
      .all([
        Api.getBanner(),
        Api.getType(),
        Api.getTypeProducts({
          key: "isHot",
          value: 1
        })
      ])
      .then(
        this.axios.spread(function(one, two, three) {
          // 三个个请求现在都执行完成
          let banner = one.data.result;
          let classify = two.data.result;
          let hots = three.data.result;
          self.$store.commit("homeModule/initAll", { banner, classify, hots });
          self.$toast.clear();
        })
      );

    // 是否登录
    if (this.isLogin) {
      let tokenString = Util.getCookies(this);

      Api.findMy({ tokenString }).then(res => {
        this.username = res.data.result[0].nickName;
      });
    }
  },
  computed: {
    isLogin() {
      return this.$store.state.isLogin;
    },
    banners() {
      return this.$store.state.homeModule.banner;
    },
    classify() {
      return this.$store.state.homeModule.classify;
    },
    hots() {
      return this.$store.state.homeModule.hots;
    }
  },
  methods: {
    toMenu(item) {
      this.$router.push({ name: "Menu", query: { type: item.type } });
    },
    toDetail(item) {
      this.$router.push({ name: "Detail", params: { pid: item.pid } });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/util.scss";
.home {
  height: 100%;
}
.van-swipe-item {
  transform: translateZ(0);
}
.top {
  height: 290px;
  position: relative;
  .top-bg {
    height: 200px;
    background: url("../assets/img/bgtop.jpg") no-repeat;
    background-size: cover;
    position: relative;
    .top-content {
      top: 50px;
      left: 0;
      position: absolute;
      width: 100%;
      padding: 0px 20px;
      z-index: 2;
      color: #fff;
      box-sizing: border-box;
      .title {
        font-size: 28px;
        margin-bottom: 10px;
      }
      .content {
        line-height: 20px;
        font-size: 18px;
      }
    }
    &::before {
      content: "";
      position: absolute;
      width: 100%;
      height: 200px;
      background: url("../assets/img/bgtop.jpg") no-repeat;
      background-size: cover;
      filter: blur(3px);
      z-index: 1;
    }
  }

  .top-banner {
    width: 340px;
    position: absolute;
    left: 50%;
    bottom: 10px;
    transform: translateX(-50%);
    height: 160px;
    z-index: 3;
    // background-color: #e4e393;
  }
}
.banner {
  width: 100%;
  height: 160px;
  background-repeat: no-repeat;
  background-size: cover;
}

.middle {
  width: 100%;
  height: 130px;
  .middle-list {
    display: flex;
    overflow-x: auto;
    .middle-item {
      flex-shrink: 0;
      width: 125px;
      height: 130px;
    }
  }
}
.middle-item {
  .item-img {
    margin: 5px auto;
    width: 90px;
    height: 90px;
    img {
      width: 100%;
      border-radius: 50%;
      border: 1px solid #999;
    }
  }
  .item-title {
    width: 100%;
    height: 30px;
    line-height: 30px;
    text-align: center;
    font-size: 16px;
    @include ellipseOne();
  }
}

.recomend-title {
  margin-top: 10px;
  font-size: 16px;
  font-weight: 600;
  height: 30px;
  line-height: 30px;
  text-align: center;
  div {
    width: 80px;
    margin: auto;
    position: relative;
    &::after,
    &::before {
      content: "";
      position: absolute;
      width: 20px;
      height: 2px;
      top: 15px;
      background-color: #000;
    }
    &::after {
      left: -25px;
    }
    &::before {
      right: -25px;
    }
  }
}
.recomend-box {
  height: calc(100% - 300px - 130px - 80px);
  .recomend-list {
    display: flex;
    padding: 10px;
    overflow-x: auto;
    .recomend-item {
      flex-shrink: 0;
      height: 137px;
      width: 137px;
      margin-right: 10px;
      &:last-of-type {
        margin-right: 0;
      }
    }
  }
}
.recomend-item {
  .recomend-img {
    margin: 8.5px auto;
    width: 90px;
    height: 90px;
    img {
      width: 100%;
      display: block;
      border-radius: 10px;
    }
  }
  .recomend-t {
    width: 100%;
    height: 30px;
    line-height: 30px;
    text-align: center;
    font-size: 16px;
    @include ellipseOne();
  }
}
</style>